html {
	height: 100%;
	width: 100%;
}

body {
	background-color: #4A60B2;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  height: 100%;
}
/*reset ul*/
ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}
.searchBar {
	margin: 10em auto 0;
}

.searchBar label {
	font-family: inherit;
	color: #74A6D1;
	font-size: 2.5em;
	font-weight: normal;
}

.searchBar input {
		font-family: inherit;
		font-size: 1em;
		margin-top: 1em;
		height: 100%;
		padding: 10px;
		width: 35%;
		border-radius: .5em;
}

.searchBar button {
	background-color: orange;
	font-size: 1em;
	margin-left: 1.2em;
	height: 2em;
	width: 7em;
	border-radius: .5em;
	border: none;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.8);
}

@media (max-width: 576px) {
	.searchBar input {
		width: 60%;
	}

	.searchBar button{
		background-color: orange;
		font-size: 1em;
		margin: 1em 0 0 .5em;
		height: 2em;
		width: 5em;
		border-radius: .5em;
		border: none;
		box-shadow: 0 2px 4px 0;

	}
}

@media (max-width: 768px) {
	.searchBar {
	margin-left: 2em;
}

.searchBar label {
	color: #74A6D1;
	font-size: 2em;
	font-weight: normal;
}

.searchBar input {
		font-size: 1em;
		margin-top: 1em;
		height: 100%;
		padding: 10px;
		width: 55%;
		border-radius: .5em;
}
}

@media (min-width: 992px) {


}

@media (min-width: 1200px) {
}
.navBarMain{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content:flex-end;
	        justify-content:flex-end;
	margin: 0 2vw 0 0;
/*	background-color: yellow;*/
	width: 100%;
}

.navBarMain ul {
	margin: 1vh 2vw 0 0;
	list-style: none;
	padding: 0;
	display: -webkit-flex;
	display: flex;
	/*align-items: center*/
}

.navBarMain li {
	padding: .5em 1em;
	display: inline-block;
	background-color: #7E90F2;
	border-radius: 2vh;
	font-size: 1.8vh;
}

.navBarMain a,
.navBarMain a:hover,
.navBarMain a:active {
	color: white;
	text-decoration: none;
}
.searchResultList {
	display: -webkit-flex;
	display: flex;
  background-color: rgb(116,119,209);
  border-radius: .5em;
	width: 70%;
	text-align: left;
	margin: .6em auto 0;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
}

.searchResultList p {
  height: 100%;
  width: 80%;
  margin: 0%;
  padding: 3%;
  color: #e5e5e5e5;
  display: -webkit-flex;
  display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	font-size: 2vh;
}

.searchResultList p:hover {
  background-color: rgb(116,166,209);
}

.resultHeader{
	color: #74A6D1
	}

.resultBtn{
	background-color: #FA9252;
	border-radius: .5em;
/*	align-items: center;*/
	border: none;
	width: 20%;
	height: 97%;
	padding: 1.5%;
	margin: 1.5%;
	font-size: 1.5vh;

}

.resultBtn:hover,
.resultBtn:active {
	background-color: #FDBD96;
}


@media (max-width: 576px) {
	.searchResultList{
		display: -webkit-inline-flex;
		display: inline-flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
/*		justify-content: center;*/
	}

	.resultBtn {
		/*align-self: auto;*/
		margin: 0 10% 4% 10%;
		font-size: 1.4vh;
		height: 50%;
		width: 80%;
	}
	.searchResultList p {
  height: 100%;
  width: 80%;
 	text-align: center;
  padding: .4em 0em 0em .4em;
  color: #e5e5e5e5;
  display: -webkit-flex;
  display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	font-size: 1.4vh;
	}
}



@media (max-width: 1023px) and (min-width: 812px) {
.searchResultList{
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
	}

	.resultBtn {
	/*align-self: auto;*/
	border: inherit;
	width: 20%;
	height: 97%;
	padding: 1.5%;
	margin: 1.5%;
	font-size: 3vh;
	}
	.searchResultList p {
  height: 100%;
  width: 80%;
  margin: 0%;
  padding: 3%;
  color: #e5e5e5e5;
  display: -webkit-flex;
  display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	font-size: 4vh;
	}
}
.carParkResult h2,
.carParkResult h3 {
	font-family: inherit;
	color: orange;
}

.carParkResult p {
	color: #e5e5e5e5;
}

.carParkResult ul {
	color: #e5e5e5e5;
}

.carParkResult {
	margin: 0 auto;
	width: 70%;
	text-align: left;
}

.titleHightlight {
	color: orange;
}

#map {
	margin-top: 10px;
  height: 60vh;
}

@media (max-width: 576px) {
	#map {
	margin-top: 10px;
  height: 50vh;
}
}
.footerDiv {
	margin-top: 35px;
}

h3 {
	color: white;
}
